.items {
   &__wrapper {
      display: flex;
      flex-wrap: wrap;
      row-gap: 30px;
   }
}
.item {
   width: 100%;
   border: 0.3px solid #828282;
   border-radius: 16px;
   @media (min-width: 1070px) {
      display: flex;
   }
   &__img {
      width: 100%;
      height: 220px;
      position: relative;
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
         border-radius: 16px 16px 0 0;
      }
      @media (min-width: 1070px) {
         max-width: 475px;
         height: 100%;
         img {
            border-radius: 16px 0 0 16px;
         }
      }
      @media (min-width: 1520px) {
      }
   }
   &__text {
      padding: 14px 12px 19px 12px;
      @media (min-width: 1070px) {
         display: flex;
         flex-direction: column;
         padding: 16px 17px 26px 23px;
      }
   }
   &__mark {
      font-size: 12px;
      font-weight: 600;
      line-height: 166%;
      text-transform: uppercase;
      position: absolute;
      top: 40px;
      left: 0;
      z-index: 1;
      padding: 8px 17px;
      background-color: #fed74b;
      &--second {
         top: 0;
         background-color: #7553ff;
         color: #fff;
         border-radius: 15px 0 12px 0;
      }
      &--third {
         background-color: #099ce8;
         color: #fff;
      }
   }

   &__duration {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      span {
         font-size: 12px;
         font-weight: 400;
         line-height: 166%;
         color: #9e9e9e;
         margin-left: 10px;
      }
      @media (min-width: 1070px) {
         order: 2;
         font-size: 14px;
         line-height: 142%;
         margin-bottom: 14px;
      }
   }

   &__title {
      margin-top: 0;
      font-size: 14px;
      font-weight: 600;
      line-height: 157%;
      color: #404040;
      margin-bottom: 18px;
      @media (min-width: 1070px) {
         order: 1;
         font-size: 26px;
         line-height: 130%;
         color: #000;
         margin-bottom: 11px;
      }
   }

   &__list {
      margin: 0;
      padding: 0;
      list-style: none;
      margin-bottom: 7px;

      li {
         font-size: 14px;
         font-weight: 400;
         line-height: 142%;
         color: #4b4b4b;
         position: relative;
         padding-left: 36px;
         &:not(:last-child) {
            margin-bottom: 9px;
         }
         &:before {
            content: "";
            width: 16px;
            height: 16px;
            background: url("../assets/img/check.svg") center no-repeat;
            position: absolute;
            top: 2px;
            left: 0;
         }
         @media (min-width: 650px) {
            &:last-child {
               display: flex;
               row-gap: 10px;
               white-space: nowrap;
               // align-items: center;
            }
         }
         @media (min-width: 1070px) {
            &:last-child {
               align-items: start;
            }
         }
      }
      @media (min-width: 1070px) {
         order: 3;
         li {
            font-size: 18px;
            line-height: 111%;
            padding-left: 30px;
            margin-bottom: 11px;
            &:last-child {
               justify-content: start;
            }
         }
      }
   }
   &__hiddentimes {
      align-self: start;
      display: flex;
      flex-wrap: wrap;
      row-gap: 8px;
      background-color: #fff;
      border-radius: 5px;
      z-index: 2;
      &.activefullhiden {
         .item__times--showmore {
            min-width: 65px;
            padding: 2px;
            margin-right: 100%;
         }
      }
   }

   &__times {
      margin-top: 7px;
      display: flex;
      flex-wrap: wrap;
      row-gap: 6px;
      position: relative;
      @media (min-width: 650px) {
         margin-left: 10px;
         margin-top: 0;
      }
      @media (min-width: 1070px) {
         margin: 0 0 0 5px;
         flex-wrap: nowrap;
      }
   }

   &__times-btn {
      background: none;
      border: 0;
      font-family: inherit;
      background-color: #d9e8ff;
      padding: 3px 14px;
      border-radius: 11px;
      font-size: 14px;
      font-weight: 400;
      line-height: 142%;
      color: #343434;
      cursor: pointer;
      transition: all 0.2s linear;
      &:not(:last-child) {
         margin-right: 4px;
      }
      &:hover {
         background-color: #6ba6ff;
         color: #fff;
         box-shadow: 0 2px 4px rgba(#000, 0.25);
      }
      &.activebtn {
         background-color: #2575ee;
         color: #fff;
      }
      @media (min-width: 1070px) {
         &:not(:last-child) {
            margin-right: 6px;
         }
      }
   }
   &__times--showmore {
      @media (min-width: 1070px) {
         text-align: center;
         width: 64px;
         padding: 0;
      }
   }
   &__bottom {
      margin-top: 35px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      @media (max-width: 374px) {
         flex-direction: column;
         row-gap: 10px;
      }
      @media (min-width: 1070px) {
         order: 4;
         justify-content: start;
         margin-top: 28px;
         margin-bottom: 0;
      }
   }

   &__price {
      display: flex;
      flex-direction: column;
      @media (min-width: 1070px) {
         margin-right: 12px;
      }
   }

   &__mainprice {
      font-size: 30px;
      font-weight: 400;
      line-height: 100%;
      color: #000;
   }

   &__secondprice {
      font-size: 10px;
      font-weight: 400;
      line-height: 200%;
      color: #000;
   }

   &__button {
      border: 0;
      background: none;
      font-family: inherit;
      font-size: 14px;
      font-weight: 400;
      line-height: 142%;
      padding: 12px 43px;
      border-radius: 20px;
      background-color: #fed74b;
      border: 0.5px solid #cba500;
      cursor: pointer;
      transition: all 0.2s ease-in;
      &:hover {
         background-color: #f87326;
         color: #fff;
      }
      &--second {
         padding: 12px 60px;
         border-radius: 10px;
      }
   }
}
